.col {
  display: flex;
  flex-direction: column;
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  flex-shrink: 0;
  margin-bottom: 1rem;
  width: calc(100% - 0.6rem);
}

.col-12 {
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .col-1 {
    flex-basis: calc(8.33% - 0.6rem);
  }
  .col-2 {
    flex-basis: calc(16.66% - 0.6rem);
  }
  .col-3 {
    flex-basis: calc(25% - 0.6rem);
  }
  .col-4 {
    flex-basis: calc(33.33% - 0.6rem);
  }
  .col-5 {
    flex-basis: calc(41.66% - 0.6rem);
  }
  .col-6 {
    flex-basis: calc(50% - 0.6rem);
  }
  .col-7 {
    flex-basis: calc(58.33% - 0.6rem);
  }
  .col-8 {
    flex-basis: calc(66.66% - 0.6rem);
  }
  .col-9 {
    flex-basis: calc(75% - 0.6rem);
  }
  .col-10 {
    flex-basis: calc(83.33% - 0.6rem);
  }
  .col-11 {
    flex-basis: calc(91.66% - 0.6rem);
  }
  .col-12 {
    flex-basis: calc(100% - 0.6rem);
  }
}
